import { Component, ViewChild  } from '@angular/core';
import {ViewController, IonicPage, NavController, NavParams } from 'ionic-angular';

/**
 * Generated class for the VervideoHtmlPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-vervideo-html',
  templateUrl: 'vervideo-html.html',
})
export class VervideoHtmlPage {
  
  @ViewChild('myvideo') myVideo: any;

  titulox: any;
  linkx: any;
  html:any;
  items: any=[];   

  constructor(public viewCtrl: ViewController,public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {   
    this.titulox='Video';
    //  this.linkx=this.navParams.data._id;
  this.html=this.navParams.data.video
 
 

   let video = this.myVideo.nativeElement;
   video.src = this.navParams.data.video ;
  video.play()

  }

  
  cancel() {
    this.viewCtrl.dismiss();
  }

}



